<template>
  <div class="loading">
  	<img src="./loading.svg">
  	<p class="desc">{{title}}</p>
  </div>
</template>

<script>

export default {
	props: {
		title: {
			type: String,
			default: '正在拼命加载...'
		}
	}
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
 @import "~styles/variable"

  .loading
    width: 100%
    text-align: center
    .desc
      line-height: 20px
      font-size: $font-size-small
      color: $color-text-l

</style>



